:root {
    --backgroundColor: rgba(246, 241, 209);
    --colorShadeA: rgb(106, 163, 137);
    --colorShadeB: rgb(121, 186, 156);
    --colorShadeC: rgb(150, 232, 195);
    --colorShadeD: rgb(187, 232, 211);
    --colorShadeE: rgb(205, 255, 232);
}

.buttonWrap {
    padding-bottom: 30px;
    margin-top: 10px;
    text-align: center;
}

.button {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    vertical-align: middle;
    text-decoration: none;
    font-size: 1rem;
    color: var(--colorShadeA);
    font-weight: 700;
    text-transform: uppercase;
    font-family: inherit;
    margin-bottom: 20px;
}

.button+.button {
    margin-left: 16px;
}

.bigButton {
    padding: 10px;
    border: 2px solid var(--colorShadeA);
    border-radius: 10px;
    background: var(--colorShadeE);
    transform-style: preserve-3d;
    transition: all 175ms cubic-bezier(0, 0, 1, 1);
}

.bigButton::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--colorShadeC);
    border-radius: inherit;
    box-shadow: 0 0 0 2px var(--colorShadeB), 0 10px 0 0 var(--colorShadeA);
    transform: translate3d(0, 10px, -1px);
    transition: all 175ms cubic-bezier(0, 0, 1, 1);
}

.bigButton:hover {
    background: var(--colorShadeD);
    transform: translate(0, 5px);
}

.bigButton:hover::before {
    transform: translate3d(0, 10px, -1em);
}

.bigButton:active {
    transform: translate(0, 10px);
}

.bigButton:active::before {
    transform: translate3d(0, 0, -1em);
    box-shadow: 0 0 0 2px var(--colorShadeB), 0 5px 0 0 var(--colorShadeB);
}

.formGroupWrap {
    display: flex;
    border: 1px solid #dbdbdb;
    margin-top: 16px;
    padding: 16px;
    flex-wrap: wrap;
    background-color: #e3e3e3;
    border-radius: 10px;
}

.formGroup {
    display: flex;
    flex-direction: column;
    width: 800px;
    max-width: 100%;
    margin: 10px auto;
}

.formGroup>label {
    margin-bottom: 10px;
    font-size: 1.2rem;
    color: var(--text-color);
}

.formGroup>textarea {
    height: 100px;
    border: 1px solid #dbdbdb;
    border-radius: 10px;
    padding: 10px;
    font-size: 1rem;
    resize: none;
    outline: none;
}

.textEditorWrap {
    height: 150px;
    border: 1px solid #dbdbdb;
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
}

.textEditorWrap_services {
    height: auto;
    border: 1px solid #dbdbdb;
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
}

.formGroup>input {
    height: 40px;
    border: 1px solid #dbdbdb;
    border-radius: 5px;
    padding: 0 5px;
    font-size: 1rem;
    outline: none;
}

.title {
    color: white;
    text-shadow: #ff152c 1px 1px 3px, #ff152c 1px 1px 3px, #ff152c 1px 1px 3px, #ff152c 1px 1px 3px, #ff152c 1px 1px 3px, #ff152c 3px 3px 3px;
    font-size: 2rem;
}

.textCenter {
    text-align: center!important;
}

.selectBox {
    margin: 10px 0;
    font-size: 1rem;
    height: 40px;
}

.formGroupHome {
    display: flex;
    align-items: center;
}

.selectBoxHome {
    width: 150px;
    height: 40px;
    margin: 0 16px;
}

.buttonNm {
    width: 100px;
    margin-right: 24px;
    color: white;
    background-color: green;
    border: none;
    border-radius: 10px;
    padding: 10px 0;
    cursor: pointer;
}

.buttonNm.disabled {
    opacity: 0.5
}

.btnInputFileHidden {
    cursor: pointer;
    background-color: darkgreen;
    width: 180px;
    padding: 10px 0;
    border-radius: 10px;
    text-align: center;
    color: white!important;
}

.btnSave {
    padding: 5px 10px;
    margin: 10px;
    background-color: aquamarine;
    border-radius: 5px;
    border: 1px solid #dbdbdb;
    cursor: pointer;
}

.btnSave:active {
    opacity: 0.5;
}

.btnSave.active {
    opacity: 0.5;
}

.textEditorNm {
    height: 70px;
    padding: 10px;
    border: 1px solid #dbdbdb;
    border-radius: 5px;
}

.btnDelete {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #dbdbdb;
    background-color: #f73333;
    color: white;
    margin-left: 16px;
}