.container {
    width: 100%;
    position: relative;
    width: 100%;
    position: relative;
    /* background: linear-gradient(180deg, rgba(236, 236, 236, 1) 0%, rgba(140, 140, 140, 0.6979166666666667) 100%); */
}

.body {
    width: 80%;
    padding: 15px 30px 10px 10px;
}

.sideBarRight {
    height: 400px;
    overflow: auto;
    position: absolute;
    top: 20px;
    right: 20px;
    border-radius: 10px;
    width: 20%;
    background: linear-gradient(0deg, rgba(236, 236, 236, 1) 0%, rgba(140, 140, 140, 0.6979166666666667) 100%);
}

.menuItem {
    height: 40px;
    cursor: pointer;
    position: relative;
}

.menuItem:hover {
    height: auto;
}

.menuItem:hover>.menuList {
    display: block;
}

.menuItem::before {
    content: "";
    display: block;
    width: 100%;
    height: 40px;
    top: 0;
    left: 0;
    background-color: green;
    transform: scaleX(0);
    transform-origin: 0 0;
    transition: all 0.3s;
}

.menuItem:hover::before {
    transform: scaleX(1);
}

.menuItem:hover>.titleMenuItem {
    color: #fff;
}

.titleMenuItem {
    font-size: 1.2rem;
    position: absolute;
    text-align: left;
    top: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    color: #333;
    padding: 0 10px;
}

.menuList {
    display: none;
    list-style: none;
}

.subMenuItem {
    height: 30px;
    line-height: 30px;
    width: 100%;
    padding: 0 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.subMenuItem:hover {
    background-color: rgb(255, 21, 44);
    color: white;
}