.boxWrap {
    width: 100%;
    height: 100%;
    border-radius: 4px;
}

.boxWrap:hover {
    border: 1px solid #3B404C;
}

.boxInput {
    width: 100%;
    height: 75%;
    outline: none;
    padding: 8px 10px;
    position: relative;
    font-size: 0.9rem;
    color: #000;
    border-radius: 4px;
    background-color: white;
    z-index: 2;
    overflow: auto;
}

.boxInput>input {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    outline: none;
    padding: 8px 8px 40px;
    position: absolute;
    border: none;
}

.boxInput>input::placeholder {
    font-size: 16px;
    color: #BBB8BC;
}

.formBtn {
    width: 100%;
    height: 25%;
    background-color: #fff;
    border-radius: 4px;
    display: flex;
    justify-content: flex-end;
}

.iconEditor {
    cursor: pointer;
    display: flex;
    align-items: center;
}

.iconEditor.active img {
    opacity: 1;
}

.iconEditor+.iconEditor {
    margin-left: 16px;
}

.iconEditor:last-child {
    margin-right: 16px;
    position: relative;
}

.iconEditor img {
    width: 14px;
    height: 14px;
    opacity: 0.6;
}

.boxEmotion {
    position: absolute;
    display: flex;
    width: 200px;
    height: 200px;
    overflow: auto;
    flex-wrap: wrap;
    bottom: 100%;
    left: 10px;
    z-index: 2;
}

.boxEmotion>input {
    display: inline-block;
    width: 28px;
    border: none;
    height: 28px;
    text-align: center;
    outline: none;
    cursor: pointer;
    background-color: beige;
}

@media screen and (max-width: 850px) {
    .formBtn {
        display: none;
    }
    .boxInput {
        height: 100%;
        border-radius: 10px;
    }
}