.container {
    width: 100vw;
    position: relative;
    height: 100vh;
    background-color: #dbdbdb;
}

.form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(97, 93, 93, 0.5);
    width: 30%;
    height: 50%;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.formUserName,
.formPassword,
.btnLoginBox {
    width: 100%;
    height: 20%;
    display: flex;
}

.inputUserName,
.inputPassword {
    width: 80%;
    height: 60%;
    margin: auto;
    border-radius: 10px;
    outline: none;
    border: none;
    padding: 0 10px;
    font-size: 1rem;
}

.btnLogin {
    width: 80%;
    height: 60%;
    font-size: 1rem;
    font-weight: bold;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    border: 2px solid #dbddbd;
    border-radius: 10px;
    color: #fff;
    margin: auto;
}

.btnLogin:active {
    color: red;
}