.footer {
    height: 30px;
}

.history-banner {
    width: 100%;
    display: block;
    position: relative;
    background-size: "100% 100%";
    height: 190%;
}

.centerBtnHeader {
    margin-top: -5px;
}

@media screen and (min-width: 1910px) {
    html {
        font-size: 19px;
    }
    .text-height {
        line-height: 2.8vh;
    }
    .text-height-history {
        line-height: 3.5vh;
    }
    .text-height-overview {
        line-height: 4.2vh;
    }
    .text-height-outstanding {
        line-height: 3.5vh;
    }
    .text-height-achivements {
        line-height: 3.85vh;
    }
    .header {
        height: 80px;
    }
    .home-body {
        height: calc(100vh - 180px);
    }
    .home-footer {
        height: 100px;
    }
    .body {
        height: calc(100vh - 80px - 30px);
    }
    .banner {
        height: 90px;
        top: 80px;
    }
    .root-body {
        min-width: 1360px;
        max-width: 1360px;
        width: 100%;
        margin: 0 auto;
    }
    .left-banner-1 {
        position: fixed;
        top: 100px;
        height: calc((100vh - 100px - 42px) / 2);
        width: 200px;
        left: calc(((100vw - 1360px) / 2) - 220px);
        /* left: 10px; */
    }
    .left-banner-2 {
        position: fixed;
        bottom: 40px;
        height: calc((100vh - 100px - 42px) / 2);
        width: 200px;
        left: calc(((100vw - 1360px) / 2) - 220px);
        /* left: 10px; */
    }
    .right-banner {
        position: fixed;
        top: 100px;
        height: calc(100vh - 100px - 40px);
        width: 200px;
        right: calc(((100vw - 1360px) / 2) - 220px);
    }
    .padding {
        padding: 30px 0;
    }
    .padding-left {
        padding-left: 30px;
    }
    .padding-right {
        padding-right: 30px;
    }
    .padding-top {
        padding-top: 30px;
    }
    .padding-bottom {
        padding-bottom: 16px;
    }
    .padding-menu-title {
        padding: 5px 30px;
    }
    .padding-content {
        padding: 30px 0 30px 0;
    }
    .padding-header {
        padding: 30px 0 0 0;
    }
    .margin-right {
        margin-right: 30px;
    }
    .header-button-width {
        width: 140px;
    }
    .history-content-title {
        font-size: 1.5rem;
        text-transform: uppercase;
    }
    .history-padding-content {
        padding: 40px 0;
    }
    .overview-box-height {
        height: 130px;
    }
    .overview-box-margin {
        margin-right: 40px;
    }
}

@media screen and (min-width: 1910px) and (min-height:980px) {
    .padding-content {
        padding: 30px 0 50px 0;
    }
}

