/*  mobile */

@media screen and (max-width: 850px) {
    * {
        font-size: 1rem;
    }
    .footer {
        height: 40px;
    }
    .centerBtnHeader {
        font-size: 0.7rem;
    }
    .home-body {
        overflow-x: hidden;
        height: calc(var(--window-height) - 170px);
    }
    .body {
        height: auto;
    }
    .padding {
        padding: 0;
    }
    .padding-right {
        padding-right: 0;
    }
    .history-content-title {
        font-size: 1.2rem;
    }
    .overview-box-margin {
        margin-right: 0;
    }
    .hideOnMobile {
        display: none;
    }
    .MuiPaginationItem-root {
        height: 24px!important;
        min-width: 24px!important;
    }
    .messengerText,
    .messenger {
        display: none
    }
}