@keyframes moveMenuItem {
    from {
        transform: translateX(30%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes move {
    0% {
        transform: scaleY(1.5) scaleX(0.5);
        opacity: 0;
    }
    50% {
        transform: scaleY(0.5) scaleX(1.5);
    }
    100% {
        transform: scaleY(1) scaleX(1);
        opacity: 1;
    }
}

.titleMenu {
    animation-name: move;
    animation-duration: .6s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: backwards;
}

.button-select {
    background-color: #cccccc !important;
}

@keyframes moveUpdown {
    0% {
        transform: translateY(-10px);
    }
    50% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-10px);
    }
}

.messenger {
    background-color: #ffffff;
    cursor: pointer;
    position: fixed;
    bottom: 2rem;
    right: 10px;
    box-shadow: none;
    border-radius: 48px;
    z-index: 1000;
    animation: moveUpdown linear 2s infinite;
}

.messengerText {
    position: absolute;
    right: 5px;
    font-size: 12px;
    display: block;
    color: white;
}

.messenger.home {
    bottom: 25px;
}

.boxchat {
    position: absolute;
    height: auto;
    width: 186px;
    top: -125%;
    right: 0;
    z-index: -1;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 0 10px rgb(0 0 0 / .3);
    padding: 10px;
    color: #333;
    font-size: 1rem;
    opacity: 0;
    display: block;
    transition: all .3s;
}

.messenger:hover .boxchat {
    opacity: 1;
}

.boxchat::after {
    content: "";
    display: block;
    border-style: solid;
    border-width: 10px;
    border-color: white transparent transparent transparent;
    position: absolute;
    bottom: -16px;
    right: 15px;
}

.boxChatIcon {
    position: absolute;
    top: -10px;
    background-color: white;
    border-radius: 50%;
    right: 5px;
}