.inputBox {
    width: 100%;
}

.inputText {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 8px
}

.inputValue {
    width: 100%;
    height: 25px
}

@media screen and (min-height:701px) {
    .inputValue {
        width: 100%;
        height: 40px
    }
}

@media screen and (max-width: 850px) {
    .inputText {
        font-size: 1.2rem!important;
        margin-bottom: 10px!important;
    }
    .inputBox1 {
        width: 90%;
        margin: 0 auto 10px;
    }
}