.news-main-img-post-item {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.news-main-header-type-post p {
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: underline;
}

.news-main-header-title-post {
    font-size: 1.2rem;
    margin: 8px 0;
}

.news-main-header-action {
    display: flex;
    align-items: center;
    width: 80px;
    justify-content: space-between;
}

.news-main-header-action img {
    display: block;
    margin-right: 5px;
}

.news-ralated-posts-item-see_more {
    outline: none;
    border: none;
    text-align: center;
    background-color: transparent;
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    display: block;
}

.news-main-img-post {
    margin: 16px 0;
    height: auto;
    width: 100%;
}

.news-main-content-post p,
.news-main-content-post span,
.news-main-content-post strong,
.news-main-content-post ul,
.news-main-content-post li,
.news-main-content-post b,
.news-main-content-post div {
    font-size: inherit;
}

.news-ralated-posts-list {
    list-style: none;
    height: 100%;
}

.news-ralated-posts {
    background-color: white;
    height: calc(49% - 26px);
}

.news-ralated-posts-item-title {
    font-size: 1.2rem;
    text-align: center;
    line-height: 40px;
}

.news-ralated-posts-item {
    width: 100%;
    height: calc(100% / 6);
    display: flex;
    align-items: center;
    justify-content: center
}

.news-ralated-posts-item:nth-child(1) {
    border-bottom: 1px solid #d3d3d3;
}

.news-ralated-posts-item-link {
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    font-size: 0.8rem;
    text-align: left;
    align-items: center;
    border-bottom: 1px dashed #d3d3d3;
}

.news-ralated-posts-item-link-icon {
    width: 10px;
    height: 10px;
    margin: 0 10px;
}

.news-ralated-posts-item-link-text.ellipsis_2 {
    padding-right: 16px;
}

@media screen and (min-width: 1920px) {
    .news-ralated-posts-item-link {
        border-bottom: 2px dashed #d3d3d3;
    }
}

@media screen and (max-width: 850px) {
    .news-ralated-posts-item-link {
        height: 60px;
    }
    .news-ralated-posts-item {
        height: auto;
    }
    .news-ralated-posts-list {
        height: auto;
        background-color: white;
        margin-bottom: 61px;
    }
    .news-ralated-posts-item-see_more {
        height: 40px;
    }
    .news-sidebar {
        display: block;
    }
}