@keyframes move1 {
    0% {
        left: -87%;
    }
    50% {
        left: 97%;
    }
    100% {
        left: 97%;
    }
}

@keyframes move2 {
    0% {
        left: -200%;
    }
    50% {
        left: -87%;
    }
    100% {
        left: 97%;
    }
}

.imgEffect {
    top: 0;
    left: -97%;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
}

.imgEffect:nth-child(1) {
    animation: move1 linear 14s infinite;
}

.imgEffect:nth-child(2) {
    animation: move2 linear 14s infinite;
}

@media screen and (max-width: 850px) {
    .imgEffect {
        object-fit: contain;
    }
}