.news-body {
    background-color: #ececec;
    background-attachment: fixed;
    background-repeat: no-repeat;
    --padding-content-top-bottom: 30px;
    --header-height: 60px;
    --footer-height: 30px;
    --total-height-header-footer: 90px;
}

.news-main-title {
    height: 5%;
}

.news-container {
    padding: var(--padding-content-top-bottom) 0;
}

.news-main-title h1 {
    font-size: 1.2rem;
    color: rgb(51, 51, 51);
    font-weight: bold;
    text-transform: uppercase;
    text-align: left;
    border-left: 4px solid #e01a2c;
    padding-left: 6px;
}

.news-main {
    width: 75%;
    padding-right: 15px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-sidebar {
    width: 25%;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-main-box {
    height: 52%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-sidebar-space {
    height: 5%;
}

.news-main-slider {
    width: 100%;
    height: calc(40% - 26px);
    position: relative;
}

.news-main-item-link {
    display: block;
    height: 50%;
}

.news-main-item {
    display: flex;
    height: 100%;
    padding: 20px 0;
}

.news-main-item-type,
.news-main-item-title {
    font-size: 1rem;
    color: rgb(51, 51, 51);
    text-decoration: underline;
    font-weight: 500;
    text-transform: uppercase;
}

.news-main-item-title {
    font-weight: bold;
    text-decoration: none;
}

.news-main-item-timeUpload {
    display: flex;
    align-items: center;
}

.news-main-item-timeUpload span {
    margin-left: 6px;
}

.news-main-item-content {
    width: 70%;
    padding: 0 16px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-main-pagination {
    display: flex;
    height: 26px;
    justify-content: flex-end;
}

.news-main-pagination>span:nth-child(1) {
    padding: 0 24px;
    background-color: white;
    border-radius: 5px;
    color: #000;
    font-weight: bold;
    font-size: 0.8rem;
    line-height: 26px;
}

.news-main-pagination button {
    min-width: 26px;
    height: 26px;
}


