.news-sidebar-menu {
    background-color: #fff;
    height: calc(49% - 26px);
}

.news-sidebar-menu-title>span {
    animation-name: move;
    animation-duration: .6s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: backwards;
}

.news-sidebar-menu-item {
    animation-name: moveMenuItem;
    animation-duration: .6s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: backwards;
}

.news-sidebar-menu-item .news-sidebar-menu-item-icon>img {
    filter: brightness(0.5);
}

.news-sidebar-menu-item.flash .news-sidebar-menu-item-icon>img {
    filter: brightness(2.5);
}

.news-sidebar-menu-title {
    background-color: #ff152c;
    color: white;
    font-size: 1rem;
    padding: 0 16px 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
}

.form-search-wrap {
    height: 44%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-sidebar-menu-list {
    list-style: none;
    box-shadow: 0 0 10px 4px rgb(0 0 0 / 2%);
    display: flex;
    flex-direction: column;
    height: calc(100% - 40px);
    justify-content: space-between;
}

.news-sidebar-menu-item {
    height: calc(100% / 6);
    position: relative;
}

.news-sidebar-menu-item.activeNew .news-sidebar-menu-item-icon {
    display: block;
}

.news-sidebar-menu-item-icon {
    position: absolute;
    top: 0;
    right: 15px;
    height: 100%;
    display: none;
    width: 40px;
}

.news-sidebar-menu-item-icon>img {
    height: 100%;
    object-fit: cover;
    transform: scale(5) translateY(-1px);
}

.news-sidebar-menu-item-link {
    width: 100%;
    height: 100%;
    padding: 6px 20px;
    font-size: 1rem;
    display: flex;
    align-items: center;
}

.news-sidebar-menu-item-link:hover,
.news-sidebar-menu-item-link.active {
    background-color: rgba(206, 204, 204, 0.2);
    font-weight: bold;
    text-decoration: underline;
}

.news-sidebar-search {
    background-color: white;
    border-radius: 4px;
    padding: 0 10px;
    display: flex;
    height: 26px;
}

.news-sidebar-search-input {
    border: none;
    outline: none;
    padding-left: 10px;
}

.news-sidebar-search-input::placeholder {
    font-size: 0.8rem;
    color: #4f4f4f;
}

.news-sidebar-search img {
    width: 14px;
    position: relative;
    top: 3px;
}

.news-sidebar-box-title {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-sidebar-form {
    width: 100%;
    background-color: white;
    padding: 20px;
    height: 43%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-sidebar-form-getInfo {
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 16px
}

.news-sidebar-form-title {
    font-size: 0.8rem;
    text-align: center;
    text-transform: uppercase;
    color: #ff152c;
}

.news-sidebar-form-getInfo-input {
    background-color: #ececec;
    border: none;
    outline: none;
    height: 25%;
    font-size: 1rem;
    width: 100%;
    padding-left: 16px;
}

.news-sidebar-form-getInfo-input::placeholder {
    font-size: 1rem;
    font-style: italic;
}

.news-sidebar-form-getInfo-button-submit {
    /* box-shadow: rgb(50 50 93 / 25%) 0px 50px 100px -20px, rgb(0 0 0 / 30%) 0px 30px 60px -30px, rgb(10 37 64 / 35%) 0px -2px 6px 0px inset; */
    width: 100%;
    border-radius: 8px;
    background-color: #FF152C;
    color: white;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    height: 25%;
    border-color: #e1e1e1;
    cursor: pointer;
}

.news-sidebar-form-getInfo-button-submit.success {
    opacity: 0.5;
}

.news-flash-left {
    position: absolute;
    width: 15%;
    top: 80px;
    left: -50px;
}

.news-flash-left:nth-child(2) {
    top: 352px;
    left: -50px;
}

.news-flash-left img {
    width: 100%;
    object-fit: cover;
}

.news-flash-right {
    position: absolute;
    top: 80px;
    right: -50px;
    width: 15%;
}

.news-flash-right img {
    width: 100%;
}

.news-main-item-img {
    object-fit: cover;
    height: 100%;
    border-radius: 10px;
    width: 20%;
}

@media screen and (max-height:700px) {
    .news-sidebar-menu-title {
        height: 30px;
        line-height: 30px;
        font-size: 0.9rem;
    }
    .news-sidebar-search {
        padding: 4px 10px;
    }
    .news-sidebar-search-input::placeholder {
        font-size: 0.8rem;
    }
    .news-sidebar-form-title {
        font-size: 0.7rem;
    }
}

.news-left-top-baner,
.news-left-bottom-baner,
.news-right-baner {
    position: fixed;
}

.news-button-close-banner {
    width: 18px;
    height: 18px;
    position: absolute;
    top: 4px;
    right: 8px;
    fill: white;
    cursor: pointer;
}

.box-type-post {
    display: none;
}

.news-right-baner .news-button-close-banner {
    right: 4px;
}

.news-right-baner>div {
    margin-right: auto;
    margin-left: 5px;
    position: relative;
}

.news-left-top-baner>div,
.news-left-bottom-baner>div,
.news-right-baner>div {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 90%;
}

.news-left-bottom-baner>div,
.news-left-top-baner>div {
    margin-left: auto;
    margin-right: 5px;
}

.news-left-top-baner {
    top: calc(var(--header-height) + var(--padding-content-top-bottom) + ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100*5) + ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100));
    height: calc((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2 - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100) - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100*5))/2);
    left: 0;
    padding-bottom: 1px;
}

.news-left-bottom-baner {
    top: calc(var(--header-height) + var(--padding-content-top-bottom) + ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100*5) + ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100) + (100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2 - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100) - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100*5))/2);
    left: 0;
    height: calc((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2 - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100) - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100*5))/2);
    padding-top: 1px;
}

.news-right-baner {
    height: calc(100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2 - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100) - ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100*5));
    right: 0;
    top: calc(var(--header-height) + var(--padding-content-top-bottom) + ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100*5) + ((100vh - var(--total-height-header-footer) - var(--padding-content-top-bottom)*2)/100));
}

@media screen and (max-width: 1349px) {
    .news-right-baner,
    .news-left-bottom-baner,
    .news-left-top-baner {
        width: calc((100vw - 1000px)/2);
    }
    .news-body {
        --header-height: 50px;
        --total-height-header-footer: 80px;
    }
}

@media screen and (min-width: 1350px) and (max-width: 1589px) {
    .news-right-baner,
    .news-left-bottom-baner,
    .news-left-top-baner {
        width: calc((100vw - 1100px)/2);
    }
}

@media screen and (min-width: 1590px) and (max-width: 1909px) {
    .news-right-baner,
    .news-left-bottom-baner,
    .news-left-top-baner {
        width: calc((100vw - 1200px)/2);
    }
    .news-body {
        --header-height: 70px;
        --total-height-header-footer: 100px;
    }
    .news-sidebar-menu-item-icon>img {
        height: 100%;
        object-fit: cover;
        transform: scale(4.2) translateY(-2px);
    }
    .news-sidebar-menu-item-icon {
        width: 60px;
    }
}

@media screen and (min-width: 1910px) {
    .news-right-baner,
    .news-left-bottom-baner,
    .news-left-top-baner {
        width: calc((100vw - 1360px)/2);
    }
    .news-body {
        --header-height: 80px;
        --total-height-header-footer: 110px;
    }
    .news-left-top-baner>div,
    .news-left-bottom-baner>div,
    .news-right-baner>div {
        width: 65%;
    }
    .news-left-top-baner>div,
    .news-left-bottom-baner>div {
        margin-right: 20px;
    }
    .news-right-baner>div {
        margin-left: 20px;
    }
    .news-left-top-baner .news-button-close-banner,
    .news-left-bottom-baner .news-button-close-banner {
        right: 24px;
    }
    .news-sidebar-menu-item-icon>img {
        height: 100%;
        object-fit: cover;
        transform: scale(5) translateY(-1px);
    }
    .news-sidebar-menu-item-icon {
        width: 70px;
    }
}

@media screen and (max-width: 850px) {
    .news-left-top-baner,
    .news-left-bottom-baner,
    .news-right-baner {
        display: none;
    }
    .news-main {
        width: 100%;
        height: auto;
        display: block;
    }
    .news-main-title,
    .news-main-box {
        height: auto;
    }
    .news-sidebar {
        width: 100%;
        height: auto;
        display: block;
    }
    .news-container {
        flex-wrap: wrap;
    }
    .news-main-slider {
        height: 180px;
        margin: 16px 0;
    }
    .news-main-item-link {
        height: auto;
        width: 100%;
        display: block;
    }
    .news-main-item {
        padding: 10px 0;
        display: flex;
        flex-direction: column;
    }
    .news-main-item-img {
        width: 100%;
        margin: 0 auto;
        height: 130px;
    }
    .news-main-item-title {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
    }
    .news-main-item-description {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
    }
    .news-main-item-content {
        width: 100%;
        margin-top: 10px;
        padding: 0;
    }
    .news-main-item-type,
    .news-main-item-title,
    .news-main-item-description {
        margin: 5px 0;
        text-align: justify;
    }
    .news-main-pagination {
        justify-content: center;
        margin-top: 16px;
    }
    .news-sidebar-menu {
        display: none
    }
    .news-sidebar-form {
        height: auto;
    }
    .news-sidebar-search {
        height: 50px;
        margin: 10px 0;
    }
    .news-sidebar-search label {
        line-height: 50px;
        width: 40px;
        text-align: center;
    }
    #search-news {
        width: 100%;
    }
    #search-news::placeholder {
        font-size: 1rem;
    }
    .news-sidebar-form-title {
        font-size: 0.9rem;
        margin-bottom: 8px;
    }
    .news-sidebar-box-title {
        height: auto;
    }
    .news-sidebar-form-getInfo {
        height: auto;
    }
    .news-sidebar-form-getInfo-input {
        height: 40px;
        margin-bottom: 10px;
    }
    .news-sidebar-form-getInfo-button-submit {
        height: 40px;
    }
    .showOnMobie {
        display: block;
    }
}