.slider-wrap {
    width: 100vw;
    height: 100%;
    position: relative;
}

.section-slider {
    height: 100%;
    width: 100%;
    position: absolute;
    display: none;
    transition: all 1s;
}

.section-slider.active {
    transform: translateX(0);
    display: block;
}

.slider-news-content {
    width: 90%;
    position: absolute;
    bottom: 16px;
    left: 16px;
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #333;
}

.section-slider.next {
    display: block;
    transform: translateX(100%);
}

.section-slider.prev {
    display: block;
    transform: translateX(-100%);
}

.slider-content {
    height: 100%;
}

.slider-background {
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
    filter: brightness(0.5);
}

.slider-content {
    position: absolute;
    width: 80vw;
    left: 10vw;
    right: 10vw;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

@keyframes moveTitleSlider {
    0% {
        transform: translateY(-30px);
        opacity: 0;
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes moveSeeMore {
    0% {
        transform: translateY(30px);
        opacity: 0;
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.section-slider.active .slider-content-title,
.section-slider.active .slider-content-description {
    animation-name: moveTitleSlider;
    animation-duration: .6s;
    animation-timing-function: linear;
    animation-fill-mode: backwards;
    animation-delay: 0.3s;
}

.section-slider.active .slider-content-description {
    animation-delay: 0.4s;
}

.section-slider.active .slider-content-link {
    animation-name: moveSeeMore;
    animation-duration: .6s;
    animation-timing-function: linear;
    animation-fill-mode: backwards;
    animation-delay: 0.3s;
}

.slider-content-title {
    color: #fff;
    width: 100%;
    font-size: 3.2rem;
    text-align: center;
    font-weight: 900;
    text-shadow: 0 0 5px #eee;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.slider-content-description {
    color: #fff;
    width: 100%;
    font-size: 1.5rem;
    margin-top: 25px;
    text-align: center;
    word-break: break-word;
    font-weight: 400;
}

.slider-content-description>b {
    font-size: inherit;
}

.slider-content-link {
    color: #343434;
    padding: 7px 15px;
    font-size: 1.2rem;
    margin-top: 24px;
    border-radius: 10px;
    background-color: #fff;
    display: flex;
    align-items: center;
    font-weight: 800;
}

.slider-content-link-icon {
    width: 20px;
    height: 20px;
}

.slider-current-list {
    display: flex;
    position: absolute;
    bottom: 5px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
}

.slider-current-icon {
    width: 15px;
    height: 15px;
    fill: #afafaf;
}

.slider-current-icon.news {
    width: 15px;
    height: 15px;
    fill: #d1d0d0;
}

.slider-current {
    background-color: transparent;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slider-current.active {
    border: 1px solid white;
}

.slider-current.news.active {
    border: 1px solid white;
}

.slider-current.active .slider-current-icon {
    fill: white;
    width: 17px;
    height: 17px;
}

.slider-current.news.active .slider-current-icon {
    fill: white;
    width: 17px;
    height: 17px;
}

.slider-current+.slider-current {
    margin-left: 5px;
}

.slider-btn-icon {
    fill: #fff;
    width: 1em;
    height: 1em;
    display: inline-block;
    font-size: 1.5rem;
    flex-shrink: 0;
    user-select: none;
}

.slider-btn {
    width: 34px;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid #fff;
    cursor: pointer;
}

.slider-btn.next {
    position: absolute;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);
}

.slider-btn.prev {
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
}

.btn-child {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: black;
    opacity: 0.6;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.section-slider-news {
    width: 100%;
    position: absolute;
    height: 100%;
    top: 0;
    right: 0;
    opacity: 0;
    will-change: opacity;
    /* z-index: -1; */
    transition: opacity 1s;
}

.section-slider-news.active {
    opacity: 1;
    z-index: 1;
}

.slider-news-background {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.slider-content-link-text {
    margin-top: -6px;
}

.slider-news-content-typePost {
    font-size: 1rem;
    text-transform: uppercase;
    text-decoration: underline;
    font-weight: bold;
    color: white;
}

.slider-news-content-title {
    font-size: 1rem;
    color: white;
}

.slider-news-content-timeUpload {
    display: flex;
    align-items: center;
    color: white;
}

.slider-news-content-timeUpload img {
    margin-right: 6px;
    width: 14px;
    height: 14px;
}

@media screen and (max-width: 850px) {
    .slider-news-content-typePost {
        font-size: 0.9rem;
    }
    .slider-news-content-title,
    .slider-news-content-timeUpload {
        font-size: 0.8rem;
    }
    .slider-news-content-timeUpload img {
        width: 13px;
        height: 13px;
    }
    .slider-current-list {
        bottom: 4px;
    }
    .slider-news-content {
        height: 40%;
    }
    .slider-content-title {
        font-size: 1.5rem;
    }
    .slider-content-description {
        font-size: 1.2rem;
    }
}