.step-item-cricle {
    border: 1px solid var(--color-primary);
    border-radius: 50%;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: white;
    z-index: 1;
    cursor: pointer;
}

.step-item-cricle::before {
    content: '';
    display: block;
    width: 14px;
    height: 14px;
    background-color: var(--color-primary);
    border-radius: 50%;
}

.step-item-year {
    color: #ff152c;
    cursor: pointer;
    font-size: 13px;
    font-weight: 800;
    background-color: transparent;
    position: absolute;
    top: 150%;
    left: 0;
    display: flex;
    align-items: center;
    transform: translateX(-50%);
}

.step-item-year span {
    font-size: 20px;
    margin-left: 2px;
}

.section-step {
    width: 86%;
    display: flex;
    --color-primary: #b2b2b9;
}

.step-item {
    position: relative;
    flex: 1;
    display: flex;
    justify-content: flex-end;
}

.step-item::before {
    content: '';
    display: block;
    width: 100%;
    height: 8px;
    background-color: var(--color-primary);
    position: absolute;
    top: 8px;
    right: 1px;
}

.step-item:last-child {
    flex: 2;
    justify-content: center;
}

.step-item:last-child::before {
    width: 50%;
    left: 0;
}

.step-item:last-child::after {
    content: '';
    display: block;
    height: 8px;
    background-color: var(--color-primary);
    position: absolute;
    top: 8px;
    width: 50%;
    right: 0;
    z-index: 0;
}

.step-item-cricle.active::before,
.step-item.active::before {
    background-color: red;
}

@media screen and (max-width: 850px) {
    .step-item-year {
        font-size: 10px;
        left: 10px;
    }
    .step-item-year span {
        font-size: 15px;
    }
}