.textEditWrap {
    width: 100%;
    height: 100%;
}

.iconControl {
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: flex-start;
}

.boxInput {
    width: 100%;
    height: calc(100% - 30px);
    outline: none;
    padding: 10px;
    overflow: auto;
}

.iconControlItem {
    width: 30px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.iconControlItem>img {
    width: 60%;
    height: 60%;
}