.inputBox {
    width: 75%;
    height: 80%;
}

.input {
    width: 100%;
    border: 1px solid #CECFD2;
    height: 100%;
    outline: none;
    padding: 0 4px;
    font-size: 0.9rem;
    border-radius: 6px;
    resize: none;
}

@media screen and (max-width: 850px) {
    .inputBox {
        width: 100%;
        height: 40px;
        margin-bottom: 10px;
    }
    .input {
        font-size: 1rem;
        line-height: 40px;
    }
}


