@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(90deg);
    }
    50% {
        transform: rotate(180deg);
    }
    75% {
        transform: rotate(270deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.spinner {
    position: fixed;
    top: calc(50% - 50px);
    left: calc(50% - 50px);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    background: rgb(2, 0, 36);
    background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(0, 212, 255, 1) 0%, rgba(79, 225, 255, 1) 16%, rgba(191, 244, 255, 1) 38%, rgba(255, 255, 255, 1) 100%);
    animation: rotate .5s linear infinite;
    z-index: 3000;
}

.spinner::after {
    content: "";
    display: block;
    margin: auto;
    border-radius: 50%;
    width: 90%;
    height: 90%;
    background-color: white
}