@keyframes light1 {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes move1 {
    from {
        left: -70%;
    }
    to {
        left: 0;
    }
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1200;
}

.overlay {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.3);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    animation: light1 .2s linear;
    z-index: -1;
}

.bodyMenuMobile {
    position: absolute;
    top: 0;
    left: 0;
    width: 70%;
    height: 100%;
    background-color: white;
    animation: move1 .3s linear;
    overflow-Y: auto;
}

.titleMenu,
.headMenu {
    color: var(--text-color);
    height: 48px;
    text-align: left;
    padding-left: 16px;
    display: block;
    line-height: 50px;
    font-size: 1.2rem;
    background-color: #ffffff;
    /* border-bottom: 1px solid #dbdbdb; */
    flex: 1 1;
}

.headMenu {
    display: block;
    text-align: center;
    border-bottom: 1px solid #dbdbdb;
}

.iconMenu {
    width: 60px;
    height: 100%;
    display: flex;
    cursor: pointer;
}

.iconMenu>img {
    width: 22px;
    height: 22px;
    margin: auto;
}

.boxMenu {
    display: flex;
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #dbdbdb;
}

@keyframes move3 {
    from {
        opacity: 0;
        transform: scaleY(0);
    }
    to {
        opacity: 1;
        transform: scaleY(1);
    }
}

.subMenu {
    display: none;
    transform: scaleY(1);
}

.subMenu.active {
    display: block;
    animation: move3 .3s linear;
    -o-animation: move3 .3s linear;
    -moz-animation: move3 .3s linear;
    -webkit-animation: move3 .3s linear;
}

.ContainerAddQuestion {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 36vw;
    height: auto;
    background-color: wheat;
    display: flex;
    flex-direction: column;
    padding: 16px;
    border-radius: 10px;
}

.inputQuestion,
.inputAnswer,
.inputLang {
    border: none;
    outline: none;
    height: 40px;
    font-size: 1rem;
    padding: 0 10px;
    margin: 10px 0;
}

.inputAnswer {
    padding: 10px;
    background-color: white;
    height: 150px;
    overflow: auto;
}

.title {
    font-size: 1.4rem;
    text-align: center;
    margin-bottom: 16px;
}

.btnForm {
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
}

.btnForm1 {
    padding: 10px;
    background-color: #ff5656;
    color: #fff;
}

.btnForm2,
.btnForm1 {
    cursor: pointer;
}

.btnForm1:active,
.btnForm2:active {
    opacity: 0.5;
}

.btnForm2 {
    padding: 10px;
    background-color: aqua;
}

.bodyCreateNews {
    width: 80%;
    height: 90vh;
    background-color: #ececec;
    border-radius: 10px;
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 2px 2px 10px 2px rgb(0 0 0 / 30%);
    display: flex;
    flex-direction: column;
    overflow: auto;
}

.bodyCreateTitle {
    font-size: 1.5rem;
    text-align: center;
    color: #333;
}

.formGroup {
    display: flex;
    flex-direction: column;
    padding: 0 16px;
}

.formGroupImg {
    display: flex;
    padding: 0 16px;
    align-items: center;
}

.formGroup:last-child {
    flex: 1;
}

.labelTypeNews {
    margin: 10px 0;
}

.formGroup>input {
    border-radius: 5px;
    border-color: #dbdbdb;
    outline: none;
    border-width: 1px;
    height: 30px;
    border-style: solid;
    padding: 10px;
    font-size: 1rem;
}

.formGroup>select {
    border-radius: 5px;
    border-color: #dbdbdb;
    height: 30px;
    font-size: 1rem;
    outline: none;
}

.inputContent,
.textEditorWrap {
    border: 1px solid #dbdbdb;
    border-radius: 5px;
    height: 100%;
    width: 100%;
    margin-bottom: 16px;
    padding: 10px;
    outline: none;
    background-color: white;
}

.btnCancel {
    left: 0;
    background-color: rgb(255 80 80 / 43%);
}

.btnDone {
    right: 0;
    background-color: rgb(147 255 65 / 46%);
}

.btnDone,
.btnCancel {
    display: inline-block;
    position: absolute;
    top: 0;
    width: 80px;
    padding: 6px 0;
    border: none;
    color: #333;
    border-radius: 10px;
    cursor: pointer;
}

.btnDone:active,
.btnCancel:active,
.uploadIMG:active {
    opacity: .5;
}

.uploadIMG {
    margin-left: 16px;
    border: 1px solid #dbdbdb;
    font-size: 0.9rem;
    padding: 2px;
    background-color: #d8d6d5;
    border-radius: 4px;
}

.formBtnAction>button {
    width: 50px;
    height: 25px;
    margin-right: 16px;
    background-color: aqua;
    border: 1px solid #9f9f9f;
    border-radius: 6px;
    cursor: pointer;
}

.formBtnActive {
    opacity: 0.5
}

.wrapForm {
    display: flex;
}

.wrapForm>.formGroup {
    width: 50%;
}

.bodyCreateBranch {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50vw;
    background-color: #ebebeb;
    height: auto;
    box-shadow: 0 0 14px 2px rgb(0 0 0 / 30%);
    padding: 36px 0;
}

.titleCreateBranch {
    margin-bottom: 10px;
    text-align: center;
    font-size: 1.2rem;
    color: #333;
    margin-top: -15px;
}

.btnCreateBranchR,
.btnCreateBranchL {
    position: absolute;
    top: 5px;
    border-radius: 4px;
    padding: 5px;
    border: 1px solid #8b8b8b;
    cursor: pointer;
}

.btnCreateBranchR:active,
.btnCreateBranchL:active {
    opacity: 0.5;
}

.btnCreateBranchR {
    right: 5px;
    background-color: rgb(33 255 0 / 59%);
}

.btnCreateBranchL {
    left: 5px;
    background-color: rgb(255 0 212 / 42%);
}

.bodyEditImage {
    width: 90%;
    height: 90%;
    background-color: wheat;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    padding: 20px 10px 10px;
    display: flex;
}

.titleEditImage {
    text-align: center;
    border-bottom: 1px solid #333;
    height: 40px;
    line-height: 40px;
}

.boxImageCurrent {
    width: 25%;
    border-right: 1px solid #333;
}

.boxAllImage {
    width: 75%;
    position: relative;
}

.imageCurrent {
    width: 100%;
    height: 300px;
    padding-right: 10px;
    margin-top: 10px;
}

.imageCurrent>img {
    object-fit: contain;
    width: 100%;
    height: 100%;
}

.boxImage {
    width: 100%;
    height: calc(100% - 40px);
    padding-top: 10px;
    padding-left: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.imageItem {
    width: calc(100%/3);
    height: 47.5%;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
}

.imageItem>img {
    object-fit: cover;
    width: 100%;
    cursor: pointer;
    height: 100%;
}

.imageItem.active>img {
    border: 4px solid green;
}

.btnIconImage {
    background: transparent;
    border: none;
    cursor: pointer;
    height: 40px;
    position: absolute;
}

.btnIconImage:active {
    opacity: .5;
}

.btnIconAddImage {
    top: 0;
    right: 170px;
}

.btnIconDeleteImage {
    top: 0;
    right: 120px;
    opacity: 0.5;
}

.btnIconDeleteImage_1 {
    top: 0;
    right: 90px;
    opacity: 0.5;
}

.btnIconDeleteImage.active,
.btnIconDoneImage.active,
.btnIconDeleteImage_1.active {
    opacity: 1;
}

.btnIconPreviousImage {
    transform: rotate(180deg);
    top: 0;
    left: 10px;
    border-radius: 50%;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #6e6e6e;
    background-color: aliceblue;
}

.btnIconNextImage {
    top: 0;
    right: 0;
    border-radius: 50%;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #6e6e6e;
    background-color: aliceblue;
}

.cancelImage {
    top: 5px;
    left: 5px;
    background-color: #e53f3f;
    border-radius: 10px;
    width: 60px;
    color: white;
}

.btnIconDoneImage {
    top: 0;
    right: 70px;
    opacity: 0.5;
}

.bodyEditImgPartner {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80vw;
    height: 80vh;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    border: 1px solid #dbdbdb;
    background-color: antiquewhite;
    padding: 10px;
}

.TitleEditImgPartner {
    text-align: center;
    font-size: 1.4rem;
    height: 30px;
}

.imageContainer {
    width: 100%;
    overflow: auto;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    height: calc(100% - 30px);
}

.btnDoneImgPartner {
    top: 0;
    right: 10px;
}

.btnAddImgPartner {
    top: 0;
    right: 50px;
}

.imageItemPartner {
    width: 150px;
    height: 150px;
    padding: 0 10px;
    margin-top: 10px;
}

.imageItemPartner.active>img {
    border: 4px solid green;
}

.imageItemPartner>img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.textCenter {
    text-align: center!important;
}